// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of } from 'rxjs';
import { delay } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { DebtsInfoModel } from '../models/debts-info.model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'debts-info-edit-dialog',
	templateUrl: 'debts-info-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class DebtsInfoEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	debtsInfo: DebtsInfoModel;
	debtsInfoForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	categories = [];
	clients = [];
	employees = [];
	paymentTypes = [];
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<DebtsInfoEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.debtsInfo = this.data.reading;
		this.clients = this.data.clients;
		this.categories = this.data.categories;
		this.employees = this.data.employees;

		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
		this.getPaymentTypes();
		this.getClients();
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	createForm() {
		let date = this.debtsInfo.date;
		if (this.debtsInfo.id > 0) {
			date = new Date(+new Date(this.debtsInfo.date) - this.tzoffset).toISOString().substr(0, 16);
		}

		this.debtsInfoForm = this.fb.group({
			id: [this.debtsInfo.id, Validators.required],
			// category: [this.debtsInfo.categoryId, Validators.required],
			paymentMethod: [this.debtsInfo.paymentMethodId, +this.debtsInfo.paidMoney > 0 ? Validators.required : ''],
			client: [this.debtsInfo.clientId, Validators.required],
			totalDebt: [this.debtsInfo.totalDebt, Validators.required],
			paidMoney: [this.debtsInfo.paidMoney, +this.debtsInfo.paidMoney > 0 ? Validators.required : ''],
			sendingCertificateNumber: [this.debtsInfo.sendingCertificateNumber, ''],
			date: [date, Validators.required],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.debtsInfo.id > 0) {
			return `تعديل الدين الخاص ب  '${this.debtsInfo.client}'`;
		}

		return 'اضافة دين جديد';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.debtsInfoForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	/**
 * Get payment methods
 */
	getPaymentTypes() {
		this.http.get(
			globalVariables.baseUrl + 'api/PaymentsMethods',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.paymentTypes = res.data.map(item => {
				return {
					name: item.paymentType,
					id: item.id
				};
			});

			this.debtsInfoForm.controls["paymentMethod"].setValue(this.debtsInfo.paymentMethodId);
		});
	}

	getClients() {
		this.http.get(
			globalVariables.baseUrl + 'api/Clients',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.clients = res.data.map(item => {
				return {
					name: item.name,
					id: item.id
				}
			});

			this.debtsInfoForm.controls["client"].setValue(this.debtsInfo.clientId);
		});
	}

	/**
	 * Returns prepared oil purchase
	 */
	prepareReading(): DebtsInfoModel {
		const controls = this.debtsInfoForm.controls;
		const _debtsInfo = new DebtsInfoModel();
		_debtsInfo.id = this.debtsInfo.id;
		_debtsInfo.paymentMethodId = controls.paymentMethod.value;
		_debtsInfo.clientId = controls.client.value;
		_debtsInfo.totalDebt = controls.totalDebt.value;
		_debtsInfo.paidMoney = controls.paidMoney.value;
		_debtsInfo.sendingCertificateNumber = controls.sendingCertificateNumber.value;
		_debtsInfo.date = controls.date.value;

		return _debtsInfo;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.debtsInfoForm.controls;

		/** check form */
		if (this.debtsInfoForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update Oil price
	 *
	 * @param _debtsInfo: DebtsInfoModel
	 */
	updateReading(_debtsInfo: DebtsInfoModel) {
		let debtDate = new Date(+new Date(_debtsInfo.date) - this.tzoffset).toISOString().slice(0, 16);

		this.http.put(
			globalVariables.baseUrl + 'api/ClientsDebts',
			{
				id: _debtsInfo.id,
				paymentTypeId: _debtsInfo.paymentMethodId === null ? 0 : _debtsInfo.paymentMethodId,
				debtValue: +_debtsInfo.totalDebt,
				paidValue: +_debtsInfo.paidMoney,
				// registeredByUserId: _debtsInfo.userId,
				debtDate,
				// categoryId: _debtsInfo.categoryId,
				gasStationClientId: _debtsInfo.clientId,
				consignmentNumber: _debtsInfo.sendingCertificateNumber
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _debtsInfo, isEdit: true });
			}, err => {
				let msg = 'فشل في تعديل حركة الشراء';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create Oil price
	 *
	 * @param _debtsInfo: DebtsInfoModel
	 */
	createReading(_debtsInfo: DebtsInfoModel) {
		let debtDate = new Date(+new Date(_debtsInfo.date) - this.tzoffset).toISOString().slice(0, 16);

		this.http.post(
			globalVariables.baseUrl + 'api/ClientsDebts',
			{
				// id: _debtsInfo.id,
				paymentTypeId: _debtsInfo.paymentMethodId === null ? 0 : _debtsInfo.paymentMethodId,
				debtValue: +_debtsInfo.totalDebt,
				paidValue: +_debtsInfo.paidMoney,
				// registeredByUserId: _debtsInfo.userId,
				debtDate,
				// categoryId: _debtsInfo.categoryId,
				gasStationClientId: _debtsInfo.clientId,
				consignmentNumber: _debtsInfo.sendingCertificateNumber
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _debtsInfo, isEdit: false });
			}, err => {
				let msg = 'فشل في اضافة حركة الدين';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
