export class DebtsInfoModel {
	id: number;
	userId: number;
	userName: string;
	category: string;
	categoryId: number;
	clientId: number;
	client: string;
	totalDebt: string;
	paidMoney: number;
	balance: string;
	paymentMethodId: string;
	paymentMethod: string;
	sendingCertificateNumber: string;
	date: string;

	clear() {
		this.id = 0;
		this.userName = '';
		this.category = '';
		this.totalDebt = '';
		this.paidMoney = 0;
		this.balance = '';
		this.sendingCertificateNumber = '';
		this.date = new Date().toISOString().substr(0, 16);
	}
}
