// Angular
import {Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
// Material
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
// RxJS
import {Subscription, of} from 'rxjs';
import {delay} from 'rxjs/operators';
// NGRX
import {Update} from '@ngrx/entity';
import {Store, select} from '@ngrx/store';
// State
import {AppState} from '../../../../../../core/reducers';
// CRUD
import {LayoutUtilsService, TypesUtilsService} from '../../../../../../core/_base/crud';
// Services and Models
import {selectCustomersActionLoading} from '../../../../../../core/e-commerce';
import {DebtsPaymentModel} from '../models/debts-payment.model';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import globalVariables from '../../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'debts-payments-details-edit-dialog',
	templateUrl: 'debts-payments-details-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class DebtsPaymentsDetailsEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	debt: DebtsPaymentModel;
	debtForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	// TODO: Get from Api
	paymentTypes = [];
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<DebtsPaymentsDetailsEditDialogComponent>,
				@Inject(MAT_DIALOG_DATA) public data: any,
				private fb: FormBuilder,
				private store: Store<AppState>,
				private layoutUtilsService: LayoutUtilsService,
				private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.debt = this.data.reading;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({Authorization: 'Bearer ' + this.token, responseType: 'text'});
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	createForm() {
		this.debtForm = this.fb.group({
			id: [this.debt.id, Validators.required],
			paymentType: [this.debt.paymentType, Validators.required],
			paymentValue: [this.debt.paymentValue, Validators.required],
			date: [this.debt.date, Validators.required]
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.debt.id > 0) {
			return `تعديل الدفعة`;
		}

		return 'اضافة دفعة ';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.debtForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	getPaymentTypes() {
		this.http.get(
			globalVariables.baseUrl + 'api/PaymentsMethods',
			{headers: this.headers}
		).subscribe((res: any) => {
			this.paymentTypes = res.data.map(item => {
				return {
					name: item.name,
					id: item.id
				};
			});
		});
	}

	/**
	 * Returns prepared oil purchase
	 */
	prepareReading(): DebtsPaymentModel {
		const controls = this.debtForm.controls;
		const _debt = new DebtsPaymentModel();
		_debt.id = this.debt.id;
		_debt.paymentValue = controls.paymentValue.value;
		_debt.paymentType = controls.paymentType.value;
		_debt.date = controls.date.value;

		return _debt;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.debtForm.controls;
		console.log(this.debtForm);

		/** check form */
		if (this.debtForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update Oil price
	 *
	 * @param _debt: DebtsPaymentModel
	 */
	updateReading(_debt: DebtsPaymentModel) {
		let debtDate = new Date(+new Date(_debt.date) - this.tzoffset).toISOString().slice(0, 16);

		this.http.put(
			globalVariables.baseUrl + 'api/debts/Payments',
			{
				debtId: _debt.id,
				paymentType: _debt.paymentType,
				paidMoney: +_debt.paymentValue,
				paymentDate: debtDate
			},
			{headers: this.headers}
		)
			.subscribe((res: any) => {
				this.dialogRef.close({_debt, isEdit: true});
			}, err => {
				let msg = 'فشل في تعديل الدفعة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create Oil price
	 *
	 * @param _debt: DebtsPaymentModel
	 */
	createReading(_debt: DebtsPaymentModel) {
		let debtDate = new Date(+new Date(_debt.date) - this.tzoffset).toISOString().slice(0, 16);

		this.http.post(
			globalVariables.baseUrl + 'api/debts/Payments',
			{
				fuelPurchasesId: _debt.id,
				paymentType: _debt.paymentType,
				paidMoney: +_debt.paymentValue,
				paymentDate: debtDate
			},
			{headers: this.headers}
		)
			.subscribe((res: any) => {
				this.dialogRef.close({_debt, isEdit: false});
			}, err => {
				let msg = 'فشل في اضافة الدفعة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
