// Angular
import {Component, OnInit, ElementRef, ViewChild, ChangeDetectionStrategy, OnDestroy, Input} from '@angular/core';
// Material
import {SelectionModel} from '@angular/cdk/collections';
import {MatPaginator, MatSort, MatSnackBar, MatDialog, MatTableDataSource} from '@angular/material';
// RXJS
import {debounceTime, distinctUntilChanged, tap, skip, delay, take} from 'rxjs/operators';
import {fromEvent, merge, Subscription, of} from 'rxjs';
// Translate Module
import {TranslateService} from '@ngx-translate/core';
// NGRX
import {Store, ActionsSubject} from '@ngrx/store';
import {AppState} from '../../../../../core/reducers';
// CRUD
import {LayoutUtilsService, MessageType, QueryParamsModel} from '../../../../../core/_base/crud';
// Services and Models
import {DebtsInfoModel} from '../models/debts-info.model';
// Components
import {DebtsInfoEditDialogComponent} from '../debts-info-edit/debts-info-edit.dialog.component';
// Http
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {DebtsPaymentsDialogComponent} from '../debts-info-payments/debts-payments.dialog.component';
import globalVariables from '../../../globalVariables';
import {ClientModel} from '../../../configuration/clients/models/client.model';
import {ClientsEditDialogComponent} from '../../../configuration/clients/client-edit/client-edit.dialog.component';
import {saveAs} from "file-saver";
import {jsPDF} from "jspdf";
import autoTable from "jspdf-autotable";

// Table with EDIT item in MODAL
// ARTICLE for table with sort/filter/paginator
// https://blog.angular-university.io/angular-material-data-table/
// https://v5.material.angular.io/compgetItemCssClassByStatusonents/table/overview
// https://v5.material.angular.io/components/sort/overview
// https://v5.material.angular.io/components/table/overview#sorting
// https://www.youtube.com/watch?v=NSt9CI3BXv4
@Component({
	// tslint:disable-next-line:component-selector
	selector: 'debts-info',
	templateUrl: 'debts-info.component.html',
	styleUrls: ['debts-info.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush,

})
export class DebtsInfoComponent implements OnInit, OnDestroy {
	// Table fields
	dataSource: MatTableDataSource<DebtsInfoModel>;
	displayedColumns = ['date', 'client', 'totalDebt', 'paidMoney', 'balance', 'paymentMethod', 'sendingCertificateNumber', 'actions']; // 'category',
	@ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
	@ViewChild('sort1', {static: true}) sort: MatSort;
	isLoadingResults = false;
	// Filter fields
	@ViewChild('searchInput', {static: true}) searchInput: ElementRef;
	filterStatus = '';
	selectedEmployee = '';
	selectedOilType = '';
	selectedGasPump = '';
	selectedSupplier = '';
	consignmentNumber = '';

	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	dateFrom = new Date(+new Date(this.date.getFullYear(), this.date.getMonth(), 1) - this.tzoffset).toISOString().slice(0, 16);
	dateTo = new Date(+new Date(this.date.getFullYear(), this.date.getMonth() + 1, 0) - this.tzoffset).toISOString().slice(0, 16);
	// Filter data
	// TODO: Get using APIs
	@Input() employees = [];
	@Input() oilTypes = [];
	@Input() clients = [];
	gasPumps = [];
	// Selection
	selection = new SelectionModel<DebtsInfoModel>(true, []);
	customersResult: DebtsInfoModel[] = [];
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	// Language
	languageText = {
		empty: 'لا يوجد بيانات لعرضها',
		searching: 'جار البحث ...',
		search: 'بحث',
		filterEmptyMessage: 'لا يوجد بيانات مطابقة ل ',
		confirm: 'تأكيد',
		close: 'الغاء',
		add: 'اضافة',
		itemsPerPage: 'عدد الصفوف بالصفحة:',
		firstPage: 'الصفحة الأولى',
		lastpage: 'الصفحة الأخيرة',
		previousPage: 'الصفحة السابقة',
		nextPage: 'الصفحة التالية'
	};
	// Symmary data
	totalAmount = 0;
	totalPrice = 0;

	/**
	 * Component constructor
	 *
	 * @param dialog: MatDialog
	 * @param snackBar: MatSnackBar
	 * @param layoutUtilsService: LayoutUtilsService
	 * @param translate: TranslateService
	 * @param store: Store<AppState>
	 * @param http
	 */
	constructor(
		public dialog: MatDialog,
		public snackBar: MatSnackBar,
		private layoutUtilsService: LayoutUtilsService,
		private translate: TranslateService,
		private store: Store<AppState>,
		private http: HttpClient
	) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		// Init DataSource
		this.dataSource = new MatTableDataSource();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.token = state.authToken;
			this.headers = new HttpHeaders({Authorization: 'Bearer ' + this.token, responseType: 'text'});
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);

		this.loadUsersReadingsList();
	}

	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}

	arabicRangeLabel = (page: number, pageSize: number, length: number) => {
		if (length === 0 || pageSize === 0) {
			return `0 من ${length}`;
		}

		length = Math.max(length, 0);

		const startIndex = page * pageSize;

		// If the start index exceeds the list length, do not try and fix the end index to the end.
		const endIndex = startIndex < length ?
			Math.min(startIndex + pageSize, length) :
			startIndex + pageSize;

		return `${startIndex + 1} - ${endIndex} من ${length}`;
	};

	/**
	 * Load UsersReadings List from service through data-source
	 */
	loadUsersReadingsList() {
		this.dataSource.data = [];
		this.isLoadingResults = true;

		let dateFrom = new Date(+new Date(this.dateFrom) - this.tzoffset).toISOString().slice(0, 16);
		let dateTo = new Date(+new Date(this.dateTo) - this.tzoffset).toISOString().slice(0, 16);
		let url = '?FromDate=' + dateFrom + '&ToDate=' + dateTo;
		// if (this.selectedOilType != '')
		// 	url += '&CategoryId=' + this.selectedOilType;

		if (this.selectedSupplier !== '') {
			url += '&GasStationClientId=' + this.selectedSupplier;
		}
		if (this.consignmentNumber !== '') {
			url += '&ConsignmentNumber=' + this.consignmentNumber;
		}

		this.http.get(
			globalVariables.baseUrl + 'api/ClientsDebts' + url,
			{headers: this.headers}
		)
			.subscribe((res: any) => {
				this.isLoadingResults = false;
				let data = [];
				let balance = 0;
				for (let index = 0; index < res.data.clientsDebtsResponses.length; index++) {
					const item = res.data.clientsDebtsResponses[index];
					let prevItem = null;
					let balanceElement = null;
					if (index === 0) {
						balanceElement = res.data.balances.find(element =>
							element.gasStationClientId == item.gasStationClientId
						);
					} else {
						prevItem = res.data.clientsDebtsResponses[index - 1];
					}

					if (balanceElement !== null && balanceElement !== undefined) {
						balance = balanceElement.balance;
					}

					if (prevItem !== null && prevItem.gasStationClientId !== item.gasStationClientId) {
						balanceElement = res.data.balances.find(element =>
							element.gasStationClientId == item.gasStationClientId
						);

						balance = 0;

						if (balanceElement !== null && balanceElement !== undefined) {
							balance = balanceElement.balance;
						}
					}

					balance = +(item.debtValue) - +(item.paidValue) + balance;

					data.push({
						...item,
						paymentMethod: item.paymentType,
						paymentMethodId: item.paymentTypeId,
						category: item.categoryName,
						categoryId: item.categoryId,
						date: item.debtDate,
						totalDebt: item.debtValue,
						userId: item.registeredByUserId,
						paidMoney: item.paidValue,
						balance: balance,
						clientId: item.gasStationClientId,
						client: item.gasStationClientName,
						sendingCertificateNumber: item.consignmentNumber
					});
				}

				this.dataSource = new MatTableDataSource(data);

				if (this.paginator) {
					this.paginator._intl.itemsPerPageLabel = this.languageText.itemsPerPage;
					this.paginator._intl.firstPageLabel = this.languageText.firstPage;
					this.paginator._intl.previousPageLabel = this.languageText.previousPage;
					this.paginator._intl.nextPageLabel = this.languageText.nextPage;
					this.paginator._intl.lastPageLabel = this.languageText.lastpage;

					this.paginator._intl.getRangeLabel = this.arabicRangeLabel;
				}

				this.dataSource.paginator = this.paginator;
				this.dataSource.sort = this.sort;
			}, (err) => {
				this.isLoadingResults = false;

				this.dataSource.data = [];

				if (this.paginator) {
					this.paginator._intl.itemsPerPageLabel = this.languageText.itemsPerPage;
					this.paginator._intl.firstPageLabel = this.languageText.firstPage;
					this.paginator._intl.previousPageLabel = this.languageText.previousPage;
					this.paginator._intl.nextPageLabel = this.languageText.nextPage;
					this.paginator._intl.lastPageLabel = this.languageText.lastpage;

					this.paginator._intl.getRangeLabel = this.arabicRangeLabel;
				}

				this.dataSource.paginator = this.paginator;
				this.dataSource.sort = this.sort;
			});
	}

	/**
	 * Delete Reading
	 *
	 * @param _item: OilPricesModel
	 */
	deleteReading(_item: DebtsInfoModel) {
		const _title: string = 'حذف الدين';
		const _description: string = 'هل أنت متأكد من حذف الدين؟';
		const _waitDesciption: string = 'جاري الحذف';
		const _deleteMessage = 'لقد تم الحذف';

		const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
			}

			// TODO: Delete action (API)
			this.http.delete(
				globalVariables.baseUrl + 'api/ClientsDebts/' + _item.id,
				{headers: this.headers}
			)
				.subscribe((res: any) => {
					this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);
					this.loadUsersReadingsList();
				}, err => {
					let msg = 'فشل في حذف الدين';
					if (err.error) {
						msg = err.error.msg;
					}

					this.layoutUtilsService.showActionNotification(msg);
				});
		});
	}

	/**
	 * Show add reading dialog
	 */
	addReading() {
		const newReading = new DebtsInfoModel();
		newReading.clear(); // Set all defaults fields
		this.editReading(newReading);
	}

	/**
	 * Show Edit reading dialog and save after success close result
	 * @param reading: OilPricesModel
	 */
	editReading(reading: DebtsInfoModel) {
		let saveMessageTranslateParam = reading.id <= 0 ? 'تم الاضافة بنجاح' : 'تمت التعديل بنجاح';
		const _saveMessage = saveMessageTranslateParam;
		const _messageType = reading.id > 0 ? MessageType.Update : MessageType.Create;
		const dialogRef = this.dialog.open(DebtsInfoEditDialogComponent, {
			data: {
				reading,
				categories: this.oilTypes,
				clients: this.clients,
				employees: this.employees
			}
		});
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
			}

			this.layoutUtilsService.showActionNotification(_saveMessage, _messageType);
			this.loadUsersReadingsList();
		});
	}

	/**
	 * Get file name of the exported excel file
	 */
	getExcelFileName() {
		return 'الديون' + '-' + new Date().toISOString().slice(0, 10);
	}

	/**
	 * Payment popup
	 */
	openPaymentsDialog(row) {
		const dialogRef = this.dialog.open(DebtsPaymentsDialogComponent, {
			data: {
				debtId: row.id
			},
			// height: '80%',
			width: '80%',
		});

		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
			}

			this.loadUsersReadingsList();
		});
	}

	/**
	 * Add Client Dialog
	 */
	addClient() {
		let reading = new ClientModel();
		reading.clear();
		let saveMessageTranslateParam = reading.id <= 0 ? 'تم الاضافة بنجاح' : 'تمت التعديل بنجاح';
		const _saveMessage = saveMessageTranslateParam;
		const _messageType = reading.id > 0 ? MessageType.Update : MessageType.Create;
		const dialogRef = this.dialog.open(ClientsEditDialogComponent, {
			data: {
				reading
			}
		});
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
			}

			this.layoutUtilsService.showActionNotification(_saveMessage, _messageType);
		});
	}

	downloadPdf() {
		let dateFrom = new Date(+new Date(this.dateFrom) - this.tzoffset).toISOString().slice(0, 16);
		let dateTo = new Date(+new Date(this.dateTo) - this.tzoffset).toISOString().slice(0, 16);
		let url = '?FromDate=' + dateFrom + '&ToDate=' + dateTo;
		let headers = this.headers;
		headers.set('Accept', 'application/pdf');

		this.http.get(
			globalVariables.baseUrl + 'api/ClientsDebts/Summary/pdf' + url,
			{headers, responseType: 'blob'}
		).subscribe(res => {
				saveAs(res, `ClientsDebts_to-${dateTo}.pdf`)
				console.log('ok');
			}, error1 => {
				console.log(error1.error)
			}
		);
	}

	downloadPdfV2() {
		let dateFrom = new Date(+new Date(this.dateFrom) - this.tzoffset).toISOString().slice(0, 16);
		let dateTo = new Date(+new Date(this.dateTo) - this.tzoffset).toISOString().slice(0, 16);
		let url = '?FromDate=' + dateFrom + '&ToDate=' + dateTo;

		this.http.get(
			globalVariables.baseUrl + 'api/ClientsDebts/Summary' + url,
		).subscribe((res: any) => {
				console.log('ok');
				console.log(res);
				this.downloadAsPDF(res.data)
			}, error1 => {
				console.log(error1.error)
			}
		);
	}

	public downloadAsPDF(res: any) {

		const doc = new jsPDF();

		const myFont = globalVariables.pdfFont;
		console.log(myFont);

		var callAddFont = function () {

			doc.addFileToVFS("MyFont.ttf", myFont);
			doc.addFont("MyFont.ttf", "MyFont", "normal");
		};
		jsPDF.API.events.push(['addFonts', callAddFont]);

		doc.addFileToVFS("MyFont.ttf", myFont);
		doc.addFont("MyFont.ttf", "MyFont", "normal");
		doc.setFont("MyFont");


		const columns = [['اسم الزبون', 'الديون', 'المدفوعات', 'الرصيد (الديون - المدفعوعات)']];


		let data = [];

		for (let row of res.clientDebtsSummaries) {
			data.push([row.name, row.debtValue,
				row.paidValue, row.balance]);
		}

		data.push(['', res.paidValueSum, res.debtValueSum, res.balanceSum])

		// let columnStyle = {
		// 	halign: 'center',
		// 	textColor: [0, 0, 0],
		// };
		//
		autoTable(doc,
			{
				head: columns,
				headStyles: {
					// lineWidth: 1,
					// lineColor: [0, 0, 0],
					halign: "center"
				},
				columnStyles: {
					0: {
						halign: 'center',
						textColor: [0, 0, 0],
						// lineColor: [0,0,0],
						// lineWidth: 1
					},
					1: {
						halign: 'center',
						textColor: [0, 0, 0],
						// lineColor: [0,0,0],
						// lineWidth: 1
					},
					2: {
						halign: 'center',
						textColor: [0, 0, 0],
						// lineColor: [0,0,0],
						// lineWidth: 1
					},
					3: {
						halign: 'center',
						textColor: [0, 0, 0],
						// lineColor: [0,0,0],
						// lineWidth: 1
					}
				},
				body: data,
				styles: {
					font: 'MyFont',    // <-- place name of your font here
					fontStyle: 'bold',
					fontSize: 12,
					// fillColor: [0, 0, 0]
				},
				margin: {top: 40},
				didDrawPage: (dataArg) => {
					let fromDate = new Date(+new Date(this.dateFrom) - this.tzoffset);
					let toDate = new Date(+new Date(this.dateTo) - this.tzoffset);
					var imgData = 'assets/media/logos/funny-gas-jockey.jpg';
					doc.addImage(imgData, 'JPEG', dataArg.settings.margin.left, 4, 20, 20);
					doc.text('تقرير ملخص الديون', 95, 10);
					doc.text(`من شهر: ${fromDate.getFullYear()}/${fromDate.getMonth() + 1}`, 165, 12);
					doc.text(`الى شهر: ${toDate.getFullYear()}/${toDate.getMonth() + 1}`, 165, 20);

					doc.line(0, 25, 300, 25);
				}
			});

		doc.save('table.pdf');


	}


}
